body {
  background-color: white;
}

.login h1 a {
  width: 5.25rem;
  height: 5.25rem;
  background-image: url("../images/webeez-logo-84x84.png");
  background-size: contain;
}
.login .button-primary {
  transition: all 0.3s ease-in-out;
  border-radius: 0;
  border-color: #fc653c;
  background-color: #fc653c;
}
.login .button-primary:hover, .login .button-primary:focus, .login .button-primary:hover {
  border-color: #fb3d0a;
  background-color: #fb3d0a;
}
.login .button-secondary {
  border-radius: 0;
  color: #fc653c;
}
.login .button-secondary:hover, .login .button-secondary:focus, .login .button-secondary:active {
  border-color: #fc653c !important;
  box-shadow: 0 0 0 0.0625rem #fc653c !important;
  color: #fc653c;
}
.login #login_error,
.login .message,
.login .success {
  border-left: 0.25rem solid #fc653c;
}
.login [type=text],
.login [type=password] {
  border-color: #1e1e22 !important;
}
.login [type=text]:hover, .login [type=text]:focus, .login [type=text]:active,
.login [type=password]:hover,
.login [type=password]:focus,
.login [type=password]:active {
  border-color: #fc653c !important;
  box-shadow: 0 0 0 0.0625rem #fc653c !important;
}