@import "/var/www/dev/scss/functions";
@import "/var/www/dev/scss/palette";
@import "../base/settings";
@import "../base/palette";

body {
	background-color: $color-white;
}

.login {
	h1 {
		a {
			width: rem(84px);
			height: rem(84px);
			background-image: url("../images/webeez-logo-84x84.png");
			background-size: contain;
		}
	}

	.button-primary {
		transition: all 0.3s ease-in-out;
		border-radius: 0;
		border-color: $color-primary;
		background-color: $color-primary;

		&:hover,
		&:focus,
		&:hover {
			border-color: darken($color-primary, 10%);
			background-color: darken($color-primary, 10%);
		}
	}

	.button-secondary {
		border-radius: 0;
		color: $color-primary;

		&:hover,
		&:focus,
		&:active {
			border-color: $color-primary !important;
			box-shadow: 0 0 0 rem(1px) $color-primary !important;
			color: $color-primary;
		}
	}

	#login_error,
	.message,
	.success {
		border-left: rem(4px) solid $color-primary;
	}

	[type="text"],
	[type="password"] {
		border-color: $color-dark !important;

		&:hover,
		&:focus,
		&:active {
			border-color: $color-primary !important;
			box-shadow: 0 0 0 rem(1px) $color-primary !important;
		}
	}
}
